<script>
import title from "../components/title.vue";
function formatter(number) {
  const numbers = number.toString().split("").reverse();
  const segs = [];
  while (numbers.length) segs.push(numbers.splice(0, 3).join(""));
  return segs.join(",").split("").reverse().join("");
}
export default {
  data: () => {
    return {
      list: {
        data: [
          //商铺电费列表
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
        ],
      },
      config: {
        value: 66,
      },
      nowPower: {
        number: [123456],
        content: "{nt}￥",
        formatter,
      },
      loadding: false,
    };
  },
  components: {
    "v-title": title,
  },
  mounted: function () {
    // 绘制echarts
    this.drawLine();
  },

  methods: {
    drawLine() {
      //电费面积图
      let powerChart = this.$echarts.init(
        document.getElementById("powerChart")
      );
      powerChart.setOption({
        title: {
          subtext: "月度用电（度）",
          subtextStyle: {
            color: "#fff",
            fontSize: 14,
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        grid: {
          left: "0%",
          bottom: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              color: "#fff",
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#34496F",
                type: "dashed",
              },
            },
          },
        ],
        series: [
          {
            name: "月均电费",
            type: "line",
            stack: "总量",
            areaStyle: {
              color: "#52b3d9",
            },
            emphasis: {
              focus: "series",
            },
            data: [120, 132, 101, 134, 90, 230, 210, 32, 321, 544, 657, 87],
            smooth: true,
          },
        ],
      });
      //电费柱状图
      let histogramChart = this.$echarts.init(
        document.getElementById("histogramChart")
      );
      histogramChart.setOption({
        grid: {
          left: "0%",
          bottom: "0%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
            label: {
              backgroundColor: "#6a7985",
            },
          },
          formatter: "{b}<br />{c}t",
        },
        xAxis: {
          type: "category",
          data: ["1F", "2F", "3F", "4F"],
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(98, 217, 255, .3)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            color: "#fff",
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(98, 217, 255, .3)",
            },
          },
        },
        series: [
          {
            data: [120, 200, 150, 80],
            type: "bar",
            itemStyle: {
              color: "#3EC1EF",
            },
            label: {
              show: true,
              position: "top",
              color: "#fff",
              formatter: "{c}w",
            },
          },
        ],
      });
    },
  },
};
</script>

